body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(
    linear,
    right top, left top,
    from(rgba( 7, 27, 82, 1)),
    to(rgba(0, 128, 128, 1))
  );
  background: -webkit-linear-gradient(
    right,
    rgba( 7, 27, 82, 1) 0%,
    rgba(0, 128, 128, 1) 100%
  );
  background: -o-linear-gradient(
    right,
    rgba( 7, 27, 82, 1) 0%,
    rgba(0, 128, 128, 1) 100%
  );
  background: linear-gradient(
    to left,
    rgba( 7, 27, 82, 1) 0%,
    rgba(0, 128, 128, 1) 100%
  )
}





